const routes = [{
    path: "",
    redirect: "/shopcart",
  },
  {
    path: "/home",
    component: resolve => (require(["@/views/home/home"], resolve)),
    meta: {
      title: '医院信息'
    }
  },
  {
    path: "/category",
    component: resolve => (require(["@/views/category/category"], resolve)),
    meta: {
      // title: '鹰潭市中医院'
    }
  },
  {
    path: "/shopcart",
    component: resolve => (require(["@/views/shopcart/shopcart"], resolve)),
    meta: {
      title: "医院服务"
    }
  },
  {
    path: "/profile",
    component: resolve => (require(["@/views/profile/profile"], resolve)),
    meta: {
      title: "我的"
    }
  },
  {
    name: "department",
    path: "/shopcart/department",
    component: resolve => (require(["@/views/department/department"], resolve)),
    meta: {
      title: "今日挂号"
    }
  },
  {
    name: "departlist",
    path: "/shopcart/department/departlist",
    component: resolve => (require(["@/views/department/departlist"], resolve)),
  },
  {
    name: "comfirereservsion",
    path: "/shopcart/department/comfirereservsion",
    component: resolve => (require(["@/views/department/comfirereservsion"], resolve)),

  },
  {
    name: "orderpay",
    path: "/shopcart/department/orderpay",
    component: resolve => (require(["@/views/department/orderpay"], resolve)),

  },
  {
    name: "nopatientinfo",
    path: "/shopcart/department/nopatientinfo",
    component: resolve => (require(["@/views/department/nopatientinfo"], resolve)),

  },
  {
    name: "addpatientinfo",
    path: "/shopcart/department/addinfopatient",
    component: resolve => (require(["@/views/department/addinfopatient"], resolve)),
  },

  // 新增预约门诊
  // {
  //     name: "departmentOPC",
  //     path: "/shopcart/Appointment/departmentOPC",
  //     component: DepartmentOPC
  // },

  // 门诊缴费
  {
    name: "OutpatientPayment",
    path: "/shopcart/Outpatient/outpatientPayment",
    component: resolve => (require(["@/views/Outpatient/outpatientPayment"], resolve)),

    meta: {
      title: "门诊缴费"
    }
  },
  // cs
  {
    name: "OutpatientPayment1",
    path: "/shopcart/Outpatient/outpatientPayment1",
    component: resolve => (require(["@/views/Outpatient/outpatientPayment1"], resolve)),

  },
  //跳转详情页测试

  {
    name: "OutpatientTopay1",
    path: "/shopcart/Outpatient/outpatientTopay1/:id",
    component: resolve => (require(["@/views/Outpatient/outpatientTopay1"], resolve)),

    meta: {
      title: "待缴费详情"
    }
  },

  {
    name: "OutpatientTopay",
    path: "/shopcart/Outpatient/outpatientTopay/:id",
    component: resolve => (require(["@/views/Outpatient/outpatientTopay"], resolve)),

    meta: {
      title: "待缴费详情"
    }
  }, {
    name: "OutpatientRecord",
    path: "/shopcart/Outpatient/outpatientRecord",
    component: resolve => (require(["@/views/Outpatient/outpatientRecord"], resolve)),

    meta: {
      title: '门诊缴费记录'
    }
  },
  {
    name: "OutpatientAlreadypay",
    path: "/shopcart/Outpatient/outpatientAlreadypay",
    component: resolve => (require(["@/views/Outpatient/outpatientAlreadypay"], resolve)),
    meta: {
      title: '已缴费详情'
    }
  },
  // 预交金充值
  {
    name: "themakenone",
    path: "/shopcart/Prepay/themakenone",
    component: resolve => (require(["@/views/Prepay/themakenone"], resolve)),
    meta: {
      title: "住院号未绑定"
    }
  },
  {
    name: "themakenbound",
    path: "/shopcart/Prepay/themakenbound",
    component: resolve => (require(["@/views/Prepay/themakenbound"], resolve)),
    meta: {
      title: "绑定住院号"
    }
  },
  {
    name: "myhospital",
    path: "/shopcart/Prepay/myhospital",
    component: resolve => (require(["@/views/Prepay/myhospital"], resolve)),
    meta: {
      title: "我的住院号"
    }
  },
  {
    name: "prepayment",
    path: "/shopcart/Prepay/prepayment",
    component: resolve => (require(["@/views/Prepay/prepayment"], resolve)),
    meta: {
      title: "预约金充值"
    }
  },
  {
    name: "perpayrecord",
    path: "/shopcart/Prepay/perpayrecord",
    component: resolve => (require(["@/views/Prepay/perpayrecord"], resolve)),
    meta: {
      title: "预约金充值记录"

    }
  },
  {
    name: "prepaybill",
    path: "/shopcart/Prepay/prepaybill",
    component: resolve => (require(["@/views/Prepay/prepaybill"], resolve)),
    meta: {
      title: "账单详情"
    }
  },
  {
    name: "dailylistingIn",
    path: "/shopcart/Prepay/dailylistingIn",
    component: resolve => (require(["@/views/Prepay/dailylistingIn"], resolve)),
    meta: {
      title: "每日清单"
    }
  },
  // 住院日清单
  {
    name: "inhospitalList",
    path: "/shopcart/detailedList/inhospitalList",
    component: resolve => (require(["@/views/detailedList/inhospitalList"], resolve)),
    meta: {
      title: "住院日清单"
    }
  },
  // cs预约门
  {
    name: "departments",
    path: "/shopcart/outpatientsCs/departments",
    component: resolve => (require(["@/views/outpatientsCs/departments"], resolve)),
    meta: {
      title: "选择科室"
    }
  },
  //
  {
    name: "bookingRegister",
    path: "/shopcart/outpatientsCs/bookingRegister/:userId",
    component: resolve => (require(["@/views/outpatientsCs/bookingRegister"], resolve)),
    meta: {
      title: "预约挂号"
    }
  },
];

const router = new VueRouter({
  mode: 'hash',
  routes,
});

// 

export default router;



